import { graphql } from "gatsby"
import Image from "gatsby-image"
import React from "react"
import { connect } from "react-redux"
import PageWrapper from "../components/pageWrapper"
import SEO from "../components/seo"
import ThemeSwitch from "../components/ThemeSwitch"

export const pageQuery = graphql`
  query {
    avatar: file(absolutePath: { regex: "/profile-pic.png/" }) {
      childImageSharp {
        fixed(width: 180, height: 180) {
          ...GatsbyImageSharpFixed_withWebp_tracedSVG
        }
      }
    }
    bg: file(absolutePath: { regex: "/grafisms.png/" }) {
      childImageSharp {
        fluid(maxWidth: 500, quality: 100) {
          ...GatsbyImageSharpFluid_withWebp_tracedSVG
        }
      }
    }
    site {
      siteMetadata {
        social {
          name
          link
        }
        talks {
          name
          link
          description
        }
        experiences {
          name
          link
          description
        }
        projects {
          name
          link
          description
        }
      }
    }
  }
`

const IndexPage = props => {
  const { location, data } = props
  const { avatar, site, bg } = data
  const { talks, projects, experiences, social } = site.siteMetadata || []

  const ItemList = ({ title, itens }) => (
    <div>
      <h2>{title}</h2>
      <hr />
      {itens.map(item => (
        <a
          key={item.name}
          href={item.link}
          target="_blank"
          rel={"noopener noreferrer"}
        >
          <h3>{item.name}</h3>
          <p>{item.description}</p>
        </a>
      ))}
    </div>
  )
  return (
    <PageWrapper location={location} title={"Vitor Alencar"}>
      <SEO title="Home" url="https://www.vitormalencar.com/" />
      <Image
        alt={"bg"}
        fluid={bg.childImageSharp.fluid}
        style={{ position: "fixed", zindex: -1 }}
        imgStyle={{ position: "fixed", zindex: -1 }}
      />

      <ThemeSwitch />
      <Image
        alt={"vitor"}
        fixed={avatar.childImageSharp.fixed}
        style={{ marginBottom: 0, borderRadius: `100%` }}
        imgStyle={{ borderRadius: `50%` }}
      />
      <h1>
        Hey, I'm Vitor Alencar a Front-end Engineer working at Taxfix based in
        Berlin.
      </h1>
      <div style={{ marginTop: "-20px" }}>
        {social.map(item => (
          <a
            key={item.name}
            style={{
              fontWeight: 300,
              fontSize: "12px",
              marginRight: "20px",
              fontFamily: "Merriweather",
              textTransform: "uppercase",
            }}
            href={item.link}
          >
            {item.name}
          </a>
        ))}
      </div>
      <ItemList title={"Talks"} itens={talks} />
      <ItemList title={"Projects"} itens={projects} />
      <ItemList title={"Experiences"} itens={experiences} />
    </PageWrapper>
  )
}

export default connect(null)(IndexPage)
